<template>
  <div class="set_nickname">
    <van-nav-bar
      title="工单提交"
      left-text="返回"
      right-text=""
      left-arrow
      @click-left="goBack"
    />
    <van-cell-group>
      <van-field v-model="type_name" label="工单类型" :placeholder="type_name ? '' : '工单类型'" readonly @click="showType = true" :error="!!$vuelidation.error('type_name')"/>
      <van-field v-model="title" label="工单标题" :placeholder="title ? '' : '请填写工单的标题'" :error="!!$vuelidation.error('title')"/>
      <van-field
        v-model="content"
        label="工单内容"
        type="textarea"
        maxlength="5000"
        placeholder="请填写工单内容"
        style="min-height:230px;"
      />
    </van-cell-group>

    <div class="bottom_btn">
      <van-button size="large" type="danger" @click="save">保存</van-button>
    </div>

    <van-popup v-model="showType" position="bottom">
      <van-picker
        show-toolbar
        :columns="typeColumns"
        title="选择类型"
        @cancel="showType = false"
        @confirm="onTypeConfirm"/>
    </van-popup>
  </div>
</template>


<script>
  import{Cell, CellGroup, Popup, Picker, Button, Field, Toast} from 'vant';
  import { WORK_EDIT } from '@/api/user';
  const typeColumns = [{"id":"1","text":"提问"},{"id":"2","text":"反馈"},{"id":"3","text":"讨论"},{"id":"4","text":"分享"},];

  export default {

    components:{
      [CellGroup.name]:CellGroup,
      [Cell.name]:Cell,
      [Popup.name]:Popup,
      [Picker.name]:Picker,
      [Button.name]:Button,
      [Field.name]: Field
    },

    data(){
      return {
        type_id : '',
        type_name : '',
        typeColumns,
        showType : false,
        title:"",
        content:"",
      }
    },

    computed: {
      prompt() {
        return this.bank_name ? '' : '请务必填写真实姓名以及对应的银行卡号，否则无法正常收款！'
      }
    },

    vuelidation: {
      data: {
        type_name: {
          required: {
            msg(){
              return "请选择工单类型"
            }
          }
        },
        title: {
          required: {
            msg(){
              return "请填写工单标题"
            }
          }
        },
        content: {
          required: {
            msg(){
              return "请填写工单内容"
            }
          }
        },
      }
    },

    methods: {
      save(){
        if(this.mixValid()){
          this.$reqPost(WORK_EDIT, {
            "type_id": this.type_id,
            "title": this.title,
            "content": this.content,
          }).then((res) => {
            let result = res.data;
            if(result.code!=0){
              Toast.fail(result.msg);
              return;
            }
            this.$router.go(-1);
            return this.$dialog.alert({message: '提交成功'})
          }).catch((err) => {
            // console.log(err)
          });
        }
      },

      onTypeConfirm(value, index){
        this.showType = false;
        this.type_id = value.id;
        this.type_name = value.text;
      },

      goBack() {
        this.$router.go(-1);
      }
    }
  }
</script>


<style scoped>
  .bottom_btn{
    padding: 30px 15px 0 15px;
  }
  .van-field--min-height .van-field__control {
    min-height:230px;
  }
</style>



